/* header style */

.App {
  width: 100vw;
  height: 100vh;
  background: #faf9fb;
  overflow-x: hidden;
}

.App::-webkit-scrollbar{
  display: none;
}

.header{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 5px 15px;
  height: 60px;
  width: 100%;
  background: #fff;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 101;
  box-shadow: 0px 3px 20px rgba(0, 0, 0, 0.1);
}

.logo {
  min-width: 70px;
  width: 55px;
  min-height: 60px;
  height: 62px; /* Adding height for consistency */
  border-radius: 50%; /* Creates a circular shape */
  overflow: hidden; /* Ensures the content is clipped to the circular shape */
}

.logo img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.inputBox{
  background: #fff; 
  border-radius: 25px;
  box-shadow: 0px 3px 20px rgba(0, 0, 0, 0.15); 
  display: flex;
  align-items: center;
  overflow: hidden; 
  padding: 8px 15px;
  width: 120px;
}

.inputBox.searchIcon {
  color: #787a84;
  font-size: 20px;
}
  
.inputBox input { 
  width: 100%;
  height: 100%;
  outline: none; 
  border: none;
  background: transparent;
  color: #787a84; 
  padding-left: 8px;
  font-size: 14px; 
  font-weight: 500;
}

.shoppingCart{
  position: relative;
  width: 30px;
  height: 30px;
  display: flex;
  justify-content: center;
}

.shoppingCart .cart{
  font-size: 30px;
  color: #373848;
}

.shoppingCart .cart_content{
  width: 20px;
  height: 20px;
  background-color: #B931FC;
  border-radius: 100%;
  min-width: 20px;
  min-height: 20px;
  position: absolute;
  top: -8px;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.shoppingCart .cart_content p{
  font-weight: 500;
  color: #faf9fb;
}

.profileContainer{
  display: flex;
  align-items: center;
}

.profileContainer .imgBox{
  width: 40px;
  height: 40px;
  min-width: 40px;
  border-radius: 100vw;
  background: #444;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

.profileContainer .imgBox .profilePic{
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.profileContainer .userName{
  font-size: 16px;
  margin-left: 8px;
  font-weight: 500;
  display: none;
  align-items: center;
}

.toggleMenu{
  transform: rotate(90deg);
}

.toggleMenu .toggleIcon{
  color: #373848;
  font-size: 30px;
}

/* header style end */

/* main style starts */

/* bottom menu styles */
.leftMenu{
  position: fixed;
  bottom: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 60px;
  background: #d5d5d5;
  border-radius: 25px 25px 0 0;
  z-index: 60px;
}

.leftMenu ul{
  display: flex;
}

.leftMenu ul li{
  position: relative;
  width: 60px;
  height: 60px;
  z-index: 1;
}

.leftMenu ul li a{
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  flex-direction: column;
  text-align: center;
  font-weight: 500;
}

.leftMenu ul li a .icon{
  position: relative;
  display: block;
  line-height: 70px;
  text-align: center;
  transition: 0.5s;

}

.leftMenu ul li a .icon .MuiSvgIcon-root{
  font-size: 26px;
  color: #B931FC;
}

.leftMenu ul li.active a .icon{
  transform: translateY(-26px);
}

.leftMenu ul li.active a .icon .MuiSvgIcon-root{
  color: #faf9fb;
  transition: 0.5s;
}

.indicator{
  position: absolute;
  height: 60px;
  width: 60px;
  background: #D67BFF;
  border-radius: 100vw;
  top: -50%;
  border: 6px solid #faf9fb;
  transition: 0.5s;
}

/* .indicator::before{
  content: "";
  position: absolute;
  top: 50%;
  left: -20px;
  width: 18px;
  height: 18px;
  background: transparent;
  border-top-right-radius: 18px;
  box-shadow: 0px -10px 0 0 #faf9fb;
}

.indicator::after{
  content: "";
  position: absolute;
  top: 50%;
  left: -20px;
  width: 18px;
  height: 18px;
  background: transparent;
  border-top-left-radius: 18px;
  box-shadow: 0px -10px 0 0 #faf9fb;
} */

.leftMenu ul li:nth-child(1).active ~ .indicator{ 
  transform: translateX(calc(60px * 0)); 
}

.leftMenu ul li:nth-child(2).active ~ .indicator{ 
  transform: translateX(calc(60px * 1)); 
}

.leftMenu ul li:nth-child(3).active ~ .indicator{ 
  transform: translateX(calc(60px * 2)); 
}

.leftMenu ul li:nth-child(4).active ~ .indicator{ 
  transform: translateX(calc(60px * 3)); 
}

.leftMenu ul li:nth-child(5).active ~ .indicator{ 
  transform: translateX(calc(60px * 4)); 
}

.leftMenu ul li:nth-child(6).active ~ .indicator{ 
  transform: translateX(calc(60px * 5)); 
}

.main{
  width: 100%;
  height: auto;
  margin-top: 60px;
}

.mainContainer{
  width: 100%;
  height: auto;
  background: #faf9fb;
  padding: 10px;
  padding-bottom: 100px;
}

.banner{
  width: 100%;
  height: 130px;
  border-radius: 10px;
  background: url("https://firebasestorage.googleapis.com/v0/b/fooddeliveryapp-55b3a.appspot.com/o/4.jpg?alt=media&token=579f167c-9ca8-4b47-9df1-0e3107fe3cfa&_gl=1*s2iw0k*_ga*MTY4MTUzNjY1Mi4xNjk3NDQxMjc1*_ga_CW55HF8NVT*MTY5NzY5NzM0NS41LjEuMTY5NzY5NzM1NS41MC4wLjA.")
  no-repeat center;
  background-size: cover;
  position: relative;
  display: flex;
  align-items: center;
  padding: 0px 10px;
  margin-top: 60px;
}

.deliverypic{
  position: absolute;
  left: 60%;
}

.bannerContent{
  padding: 8px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}

.bannerContent h3{
  font-size: 18px;
  font-weight: 600;
  color: #974EC3;
}

.bannerContent p{
  font-size: 14px;
  width: 200px;
  color: #974EC3;
  margin: 5px 0;
}

.bannerContent p span{
  color: #5B0888;
  font-weight: 600;
}

.bannerContent p span .MuiSvgIcon-root{
  color: #5B0888;
  font-weight: 600;
  font-size: 13px;
}

.bannerContent a{
  background: #5B0888;
  font-size: 14px;
  font-weight: 500;
  color: #faf9fb;
  padding: 5px 15px;
  border-radius: 100vw;
  cursor: pointer;
}

.bannerContent a:hover{
  box-shadow: 0px 5 px 20px rgba(0, 0, 0, 0.15);
  transition: 0.5s;
}

/* dish container */
.dishContainer{
  width: 100%;
  padding: 10px 0;
}

.menuCard{
  width: 100%;
}

.subMenuContainer,
 .viewAll{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.subMenuContainer h3{
  font-size: 16px;
  font-weight: 600;
  color: #917FB3;
}

.subMenuContainer .viewAll p{
  font-size: 12px;
  color: #B931FC;
  font-weight: 500;
}

.subMenuContainer .viewAll i{
  background: #B931FC;
  width: 20px;
  height: 20px;
  max-width: 20px;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 10px;
}

.subMenuContainer .viewAll i .MuiSvgIcon-root{
  font-size: 16px;
  color: #faf9fb;
}

.rowContainer{
  display: flex;
  align-items: center;
  overflow-x: scroll;
  overflow-y: hidden;
  margin: 10px 0;
  padding: 10px 0;
}

.rowContainer::-webkit-scrollbar{
  display: none;
}

.rowMenuCard{
  width: 80px;
  min-width: 80px;
  height: 100px;
  border-radius: 10px;
  background: #fff;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  padding: 5px;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
  transition: 0.5s;
  cursor: pointer;
  margin-right: 12px;
}

.rowMenuCard:hover, 
.rowMenuCard.active{
  background: #D67BFF;
}

.rowMenuCard .imgBox{
  width: 40px;
  height: 40px;
  min-width: 40px;
  min-height: 40px;
  border-radius: 100vw;
  background: #D67BFF;
  padding: 2px;
  display: flex;
  justify-content: center;
  align-self: center;
  transition: 0.5s;
  overflow: hidden;
}

.rowMenuCard:hover .imgBox, 
.rowMenuCard.active .imgBox{
  background: #fff;
}

.rowMenuCard h3{
  font-size: 14px;
  color: #B931FC;
  font-weight: 500;
  transition: 0.5s;
}

.rowMenuCard:hover h3, 
.rowMenuCard.active h3{
  color: #faf9fb;
}

.rowMenuCard .imgBox img{
  object-fit: contain;
  width: 100%;
  height: 100%;
  background: #D67BFF;
}

.rowMenuCard:hover .imgBox img, 
.rowMenuCard.active .imgBox img{
  width: 80%;
  height: 80%;
  transition: 0.5s;
  background: #fff;
}

.rowMenuCard .loadMenu{
  width: 20px;
  height: 20px;
  display: flex;
  justify-content: center;
  align-content: center;
  border-radius: 5px;
  background: #D67BFF;
  transition: 0.5s;
}

.rowMenuCard:hover .loadMenu, 
.rowMenuCard.active .loadMenu{
  background: #fff;
}

.rowMenuCard .loadMenu .MuiSvgIcon-root{
  font-size: 18px;
  color: #faf9fb;
  font-weight: 500;
}

.rowMenuCard:hover .MuiSvgIcon-root, 
.rowMenuCard.active .MuiSvgIcon-root{
  color: #D67BFF;
}

.dishitemContainer{
  display: flex;
  justify-content: space-around;
  align-content: center;
  flex-wrap: wrap;
}

.itemCard{
  width: 160px;
  height: 180px;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  margin: 10px 0;
}

.itemCard .imgBox{
  width: 100px;
  height: 100px;
  border-radius: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5px;
  position: absolute;
  top: 0px;
  filter: drop-shadow(8px 10px 10px #888888);
}

.itemCard .imgBox img{
  width: 100%;
  height: 80%;
  object-fit: contain;
}

.itemImg{
  position: absolute;
  top: 0;
  width: 60%;
}

.itemContent{
  background: #fff;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.15);
  border-radius: 15px;
  padding: 10px;
  padding-top: 50px;
  width: 100%;
}

.itemContent .itemName{
  font-size: 16px;
  margin: 5px;
  text-align: center;
  color: #B931FC;
}

.itemContent .bottom{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.itemContent .bottom .rating .MuiSvgIcon-root{
  font-size: 16px;
}

.purple{
  color: #B931FC !important;
}

.gray{
  color: #373848 !important;
}

.itemContent .bottom .price{
  font-size: 16px;
}

.itemContent .bottom .price span .MuiSvgIcon-root{
  color: #B931FC;
  font-weight: 800;
  font-size: 16px;
}

.itemContent .addtoCart{
  background: #B931FC;
  width: 25px;
  height: 25px;
  border-radius: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.15);
  cursor: pointer;
}

.itemContent .addtoCart .MuiSvgIcon-root{
  color: #faf9fb;
  font-size: 16px;
}

.isFavourite{
  position: absolute;
  top: 70px;
  right: 10px;
  transition: 0.5s;
}

.isFavourite .MuiSvgIcon-root{
  font-size: 18px;
  color: #ff000088;
}

.isFavourite.active .MuiSvgIcon-root{
  color: #ff0000;
  filter: drop-shadow(0px 0px 8px #ff0000);
}

/* Right menu styles */
.rightMenu{
  width: 350px;
  height: calc(100% - 60px);
  background: #fff;
  position: fixed;
  right: 0;
  top: 55px;
  z-index: 105;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px 10px;
  transform: translateX(200%);
  transition: transform 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  box-shadow: 0px 3px 20px rgba(0, 0, 0, 0.1);
}

.rightMenu.active{
  transform: translateX(0%);
}

.debitCardContainer{
  padding: 10px;
  width: 100%;
  height: auto;
}

.debitCard{
  position: relative;
  width: 100%;
  height: 170px;
  min-width: 300px;
  min-height: 170px;
  border-radius: 15px;
}

.debitCard::before{
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #B931FC;
  transform: rotate(5deg);
  border-radius: 15px;
  filter: blur(2px);
  z-index: -2;
}

.debitCard::after{
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    35deg,
    rgba(255, 255, 255, 0.3),
    rgba(155, 255, 255, 0.1)
  );
  backdrop-filter: blur(10px);
  z-index: -1;
  border: 1px solid rgba(255, 255, 255, 0.4);
}

.cardGroup{
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100%;
}

.card_logo,
.card_chip,
.card_number,
.card_name, 
.card_from,
.card_to,
.card_ring {
  position: absolute;
}

.card_logo{
  width: 60px;
  top: 30px;
  right: 30px;
  height: auto;
}

.card_chip{
  width: 40px;
  height: auto;
  top: 50px;
  left: 30px;
}

.card_number, 
.card_from,
.card_to,
.card_name{
  color: #fff;
  font-weight: 400;
  letter-spacing: 2px;
}

.card_number{
  left: 30px;
  bottom: 60px;
}

.card_name{
  font-size: 12px;
  bottom: 30px;
  left: 30px;
}

.card_from{
  font-size: 12px;
  bottom: 30px;
  right: 90px;
}

.card_to{
  bottom: 30px;
  right: 30px;
  font-size: 12px;
}

.card_ring{
  height: 400px;
  width: 400px;
  border-radius: 100vw;
  background: transparent;
  border: 50px solid rgba(255, 255, 255, 0.2);
  top: -250px;
  right: -250px;
  box-sizing: border-box;
}

.card_ring::after{
  position: absolute;
  content: "";
  height: 500px;
  width: 500px;
  border-radius: 100vw;
  background: transparent;
  border: 50px solid rgba(255, 255, 255, 0.2);
  top: 185px;
  right: -155px;
  box-sizing: border-box;
}

.cartCheckOutContainer{
  width: 100%;
  min-width: 320px;
}

.cartContainer{
  width: 100%;
  min-height: 250px;
  max-height: 280px;
  margin-top: 10px;
  overflow-y: scroll;
  overflow-x: hidden;
}

.cartContainer::-webkit-scrollbar{
  display: none;
}

.cardItem{
  display: flex;
  align-items: center;
  margin: 8px 0;
}

.cardItem .imgBox{
  width: 60px;
  min-width: 60px;
  height: 60px;
  min-height: 60px;
  background: #00ffc855;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5px;
}

.cardItem .imgBox img{
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.cardItem .itemSection{
  margin-left: 10px;
}

.cardItem .itemSection .itemName{
  font-size: 16px;
  color: #2a2b3c;
}

.cardItem .itemSection .itemQuantity{
  font-size: 16px;
  color: #2a2b3c;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 120px;
}

.cardItem .itemSection .itemQuantity span{
  color: #B931FC;
}

.cardItem .itemSection .itemQuantity .quantity{
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  width: 60px;
}

.cardItem .itemSection .itemQuantity .quantity .itemRemove,
.cardItem .itemSection .itemQuantity .quantity .ietmAdd{
  color: #2a2b3c;
  font-size: 24px;
}

.cardItem .itemPrice{
  font-size: 16px;
  font-weight: 600;
  color: #2a2b3c;
  margin-left: auto;
}

.cardItem .itemPrice .inrSign .MuiSvgIcon-root{
  font-size: 12px;
  color: #B931FC;
}

..cardItem .itemPrice .itemPriceValue{
  font-size: 16px;
  color: #373848;
}

.totalSection{
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px 8px;
  margin: 15px 0px;
}

.totalSection h3{
  font-size: 18px;
  font-weight: 600;
  color: #2a2b3c;
}

.totalSection p{
  font-size: 18px;
  font-weight: 600;
  color: #2a2b3c;
}

.totalSection p span{
  font-size: 17px;
}

.totalSection p span .MuiSvgIcon-root{
  font-size: 14px;
  color: #B931FC;
}

.checkOut{
  outline: none;
  border: none;
  width: 100%;
  padding: 10px 20px;
  background: #B931FC;
  border-radius: 100vw;
  color: #faf9fb;
  font-size: 18px;
  font-weight: 600;
  letter-spacing: 2px;
}

@media screen and (min-width: 650px) {
  .rowContainer {
    justify-content: space-between;
  }

  .inputBox {
    width: 60%;
  }
}

@media screen and (min-width: 950px) {
  .toggleMenu {
    display: none;
  }

  .profileContainer .userName {
    display: block;
  }

  .leftMenu {
    left: 25%;
    width: 50%;
  }

  .mainContainer {
    width: calc(100% - 350px);
  }
  .banner {
    height: 160px;
  }

  .deliverypic {
    position: absolute;
    left: 83.5%;
    height: 160px;
  }

  .bannerContent h3 {
    font-size: 24px;
  }

  .bannerContent p {
    font-size: 15px;
    margin: 12px 0;
    width: unset;
  }

  .bannerContent a {
    font-size: 16px;
  }

  main {
    display: flex;
  }

  .mainContainer {
    padding: 10px 30px;
  }

  .rightMenu {
    position: fixed;
    z-index: 1;
    transform: translateX(0%);
    top: unset;
    padding: 80px 15px;
  }
  
  .itemCard {
    width: 180px;
    height: 200px;
  }
}